import React from 'react'
import Card from 'react-bootstrap/Card';
import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import { Trash2 } from 'react-feather';
import { addHistory, deleteVideo } from '../Services/allapis';
import { format } from 'date-fns';
import uniqid from 'uniqid';


function Vediocard222({video,deleteFunc}) {
    const [show, setShow] = useState(false);

    const handleClose = () => setShow(false);
    const handleShow = async() => {
    setShow(true);
    //body

    //id
var id=uniqid()

//title
var video_title=video.caption

//url
var url=video.video_url

//date
var date=format(new Date(),'yyyy-MM-dd h:mm:ss a')

console.log(date);

const body={id,video_title,url,date}
if(body){
    const result=await addHistory(body)
}
    }

    const handleDelete=async(id)=>{
        const result=await deleteVideo(id)
        //  console.log(result);
        if (result.status>=200 && result.status<300) {
            // setAllVideos(result.data)
            deleteFunc(result.data)
        }
    }

    const dragStart=(e,id)=>{
        console.log("darg started... source card-"+id);


        //store dragged data
          e.dataTransfer.setData("cardId",id)
    }
    return (
        <div>
        
                <Card draggable onDragStart={(e)=>dragStart(e,video.id)} className='bg-black border mt-5' style={{ width: '18rem' }}>
                    <Card.Img onClick={handleShow} 
                        style={{ height: "250px", width: "100%" }}
                        variant="top" src={video.cover_img} />
                    <Card.Body>

                        

                        <Card.Title>
                            <h5 className='text-secondary'>{video.caption}</h5>
                            <div className='text-end'>

                                <Trash2 onClick={()=>handleDelete(video.id)} size={56} className='text-secondary btn'></Trash2>
                                {/* <i class="fa-solid fa-trash fa-2x text-secondary "></i> */}
                            </div>
                          
                        </Card.Title>

                        
                    </Card.Body>
                </Card>


                <Modal show={show} onHide={handleClose}>

                    <Modal.Body className='bg-black border border-danger'>
                        <iframe width="460" height="500" 
                        src={video.video_url}
                         title="Luminar Technolab, Data Science,  Big Data Analytics, Python, Software Testing &amp; MEAN Stack Training" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
                    </Modal.Body>
                    <Modal.Footer className='bg-black border border-danger'>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                    </Modal.Footer>
                </Modal>
      
        </div>
    )
}

export default Vediocard222
