import React, { useState } from 'react'
import { Col, Row } from 'react-bootstrap'
import Add from '../Components/Add'
import Vediocard from '../Components/Vediocard'
import Categories from '../Components/Categories'
import { Link } from 'react-router-dom'


function Home() {

  //state  for state-lifting
  const [addUpdate,setAddUpdate]=useState({})

  return (
    <div>
      <Row>

      <Col>
      <h2 className='text-light ms-5 ps-5 mt-5'><span className='text-secondary'>S</span>tart <span  className='text-secondary'>U</span>ploading <span  className='text-secondary'>V</span>ideos <span  className='text-secondary'>F</span>or <span  className='text-secondary'>F</span>ree
      </h2>

      <div>

  <Link to={'/watch-history'} style={{textDecoration:"none"}}>
  <a style={{textDecoration:"none",marginTop:"500px"}} className=' mt-5 ms-5 ps-5' >Check Watch History <i  class="fa-solid fa-clock fa-spin-pulse"></i></a>
  </Link>
   <p className='ms-5 text-secondary mt-5'> HEY IT'S THE PLACE TO UPLOAD. HAVE THE BEST</p>
      </div>
      </Col>

      <Col>
      
      <img className='mt-5 ms-5 me-1'  style={{height:"200px", borderRadius:"100px", border:"white"}} src="https://i.postimg.cc/gJ33rt9N/images-dd.jpg" alt="" />
      </Col>
      </Row>
      
      <Row className='mt-5'>
        <Col lg={2}><h3  className='text-light ms-5'>

           <Add update={setAddUpdate} > </Add>
           </h3></Col>

        <Col lg={7}><h3  className='text-light ms-5'>

          <Vediocard updatedState={addUpdate}></Vediocard>
          </h3></Col>

        <Col lg={3}><h3  className='text-light me-5'>

          <Categories> </Categories>
          </h3></Col>

      </Row>
    </div>
  )
}

export default Home