import React, { useEffect } from 'react'
import { useState } from 'react';
import Table from 'react-bootstrap/Table';
import { Trash2 } from 'react-feather';
import { deletehis, getAllHistory } from '../Services/allapis';
import { Button, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';


function History() {

const [histories,setHistories]=useState([])

const getHistories=async()=>{
    const {data}=await getAllHistory()
    setHistories(data);
    // console.log(data);
    getHistories()
}

   const removeHistory=async(id)=>{
        await deletehis(id)
         getAllHistory()
        
    }


useEffect(()=>{
    getHistories()
},[])


  return (
    <div>
      <Container>
        <h1 className='text-center p-5'><span className='text-secondary'>W</span>atch <span className='text-secondary'>H</span>istory</h1>

<div className='text-end pe-5 pb-3'>
  <Link to={'/home'}>
<Button className='btn btn-secondary rounded'>Go Back</Button>
</Link>
</div>
{
    histories.length>0?(

        <Table className='w-75 container pb-5 mb-5' striped bordered hover variant='primary'>
      <thead className='text-center fs-5 text-primary'>
        <tr>
          <th>#</th>
          <th>Date</th>
          <th>Title</th>
          <th>Video URL</th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        {
            histories?.map((i,index)=>
        <tr>
          <td>{index+1}</td>
          <td>{i?.data}</td>
          <td>{i?.video_title}</td>
          <td>{i?.url}</td>
          <td className='text-center'><Trash2 onClick={()=>{removeHistory(i?.id)}} size={50} className='btn text-white'></Trash2></td>
        </tr>
         )
       }
      </tbody>
    </Table>): <h1>No Histories</h1>
}

</Container>
    </div>
  )
}

export default History