import React, { useState } from 'react'
import { Button, Container } from 'react-bootstrap'
import Modal from 'react-bootstrap/Modal';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import uniqid from 'uniqid';
import { addVideo } from '../Services/allapis';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function Add({update}) {

//state to hold input datas
const [inputs,setInputs]=useState({

  id:"",
  caption:"",
  cover_img:"",
  video_url:""
  
})

//function for onChange
const setValues=(e)=>{

let {value,name}=e.target
// console.log(value,name);

setInputs({...inputs,[name]:value})
}

//functions to extract video url
const extractUrl=(e)=>{
  let videoUrl=e.target.value
  // console.log(videoUrl);


  if(videoUrl.includes("v=")){
    // let index=videoUrl.indexOf("v=")
    // console.log(index);

    // let subUrl=videoUrl.slice(index+2,index+13)
    // console.log(subUrl);
let subUrl=videoUrl.split("v=")[1]
console.log(subUrl);

let finalUrl=`https://www.youtube.com/embed/${subUrl}?autoplay=1`
setInputs({...inputs,["video_url"]:finalUrl})
  }
}


//function add button click
const addhandle=async()=>{
  let id=uniqid()
  // console.log(id);
  setInputs({...inputs,["id"]:id})

  const {caption,cover_img,video_url}=inputs
  
  if(caption=="" || cover_img=="" || video_url==""){

    toast.error('all inputs are required', {
      position: "top-center",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
      theme: "light",
      });
    
  }
  else{

  const result= await addVideo(inputs)
  console.log(result);



if (result.status>=200 && result.status<300) {
  
  //update state of home
  update(result.data)
  //instead of alert toast uesd
//  toast("video added successfully")

 toast.success('video added', {
  position: "top-center",
  autoClose: 2000,
  hideProgressBar: false,
  closeOnClick: true,
  pauseOnHover: true,
  draggable: true,
  progress: undefined,
  theme: "light",
  });
  setshow(false)
}
}

}

console.log(inputs);


  const [show,setshow]=useState(false);
  const handleClose=()=>setshow(false);
  const handleShow=()=>setshow(true);

  return (
    <div>
      <Container>
  <Button   variant="outline-secondary"  style={{borderRadius:"1500px", color:'white'}}><i onClick={handleShow} class="fa-solid fa-upload fa-beat-fade fa-2x "></i></Button>
     
    
     <Modal  show={show} onHide={handleClose}>
      <Modal.Body className='bg-black border'>

          <Modal.Title style={{marginTop:'25px', marginLeft:'65px'}}><b className='text-secondary  ms-5'><span className='text-light'>U</span>pload <span className='text-light'>V</span>edio</b></Modal.Title>

          <FloatingLabel
        controlId="floatingInput"
        label="Vedio Caption"
        className="mb-3 text-dark mt-3"
      >
       <Form.Control name='caption' onChange={(e)=>setValues(e)} type="text"/>
      </FloatingLabel>
 
      <FloatingLabel
        controlId="floatingInput1"
        label="Cover Image URL"
        className="mb-3 text-dark mt-3"
      >
       <Form.Control  name='cover_img' onChange={(e)=>setValues(e)} type="text"/>
      </FloatingLabel>

      <FloatingLabel
        controlId="floatingInput2"
        label="Utube Video URL"
        className="mb-3 text-dark mt-3"
      >
       <Form.Control name='video_url' onChange={(e)=>extractUrl(e)} type="text"/>
      </FloatingLabel>

      {/* <FloatingLabel controlId="floatingInput" label="Cover Image URL" className='text-dark'>
        <Form.Control type="text"/>
      </FloatingLabel> */}
        <Modal.Footer  className='bg-white'>
          <Button style={{borderRadius:"10px"}} variant="primary" onClick={handleClose}>
            Close <span className='text-secondary'>x</span>
          </Button>
          <Button  style={{borderRadius:"10px"}} variant="secondary" onClick={addhandle}>
            Add  <span className='text-primary'>+</span>
          </Button>
        </Modal.Footer>
        </Modal.Body>
      </Modal> 
      </Container>

    
      <ToastContainer />
        </div>
  )
}

export default Add