import React, { useEffect, useState } from 'react'
import Vediocard222 from './Vediocard222'
import { getAllvideos } from '../Services/allapis'
import { Col, Row } from 'react-bootstrap'


function Vediocard({updatedState}) {

const [allVideos,setAllVideos]=useState([])

//state to update delete
const [deleteUpdate,setdeleteUpdate]=useState({})

const accessAllVideos=async()=>{
  const result=await getAllvideos()
  if (result.status>=200 && result.status<300) {
    
    // console.log(result.data);
    setAllVideos(result.data)
  }
}

console.log(allVideos);

useEffect(()=>{
  accessAllVideos()
},[updatedState,deleteUpdate])

  return (
 <Row>

      {
allVideos.length>0?(
  allVideos.map(i=>

<Col lg={5} md={2}> <Vediocard222  deleteFunc={setdeleteUpdate} video={i}></Vediocard222> </Col>

    )
): <h1>No Videos In Your Collection</h1>
  
    }

    </Row>
  )
}

export default Vediocard