import React from 'react'
import { Col, Container, Row } from 'react-bootstrap';
import Navbar from 'react-bootstrap/Navbar';
import { Link } from 'react-router-dom';

function Footer() {
  return (
    <div className='text-light' style={{marginTop:"100px"}}>
<hr />

<Container>
      
      <Row >
        <Col className='ms-5 ' >
               <img src="https://i.postimg.cc/8Prqb6nf/purple.png" alt=""  style={{height:"45px" , borderRadius:"150px"}}/>
          <Navbar.Brand href="#home"><b className=' fs-4 text-secondary'><span className='text-primary'>V</span>ideo <span className='text-primary'>U</span>ploader</b></Navbar.Brand>
          <p className='mt-4 text-light'>Designed and build with all the love from the bootstrap</p>
        </Col>

        <Col className='ms-5'>

        <p className='text-light'><b>Link</b></p>
      <Link to={'/'} style={{textDecoration:"none"}}> <a className='text-secondary mt-5' style={{textDecoration:"none"}} >Landing</a> <br /> <br /> 
      </Link> 

      <Link to={'/Home'} style={{textDecoration:"none"}}> <a className='text-secondary mt-5' style={{textDecoration:"none"}} >Home</a> <br /><br />
      </Link>

      <Link to={'/watch-history'} style={{textDecoration:"none"}}> <a className='text-secondary mt-5' style={{textDecoration:"none"}} >Watch History</a><br /><br />
      </Link> 

      {/* <Link to={'/singleview'} style={{textDecoration:"none"}}> <a className='text-secondary mt-5' style={{textDecoration:"none"}}>Single View</a>
      </Link>  */}


        </Col>

        <Col >
        <p className='text-light'>Guides</p>
        <p className='text-secondary'>React</p>
        <p className='text-secondary'>React-bootstrap</p>
        <p className='text-secondary'>Routing</p>
        </Col>

        <Col  >
        <p className='text-light'> Contact Us</p>
        <input style={{borderRadius:"10px"}} type="text" className='form-control mt-2 ' placeholder='enter email'/>
        <button style={{borderRadius:"10px"}} className='btn btn-secondary mt-4 container'>send</button>
        <i class="fa-brands fa-instagram fa-1x  ms-5  mt-2"></i>
        <i class="fa-brands fa-facebook fa-1x  ms-5   mt-2"></i>
        <i class="fa-brands fa-twitter fa-1x  ms-5  mt-2"></i>
        <i class="fa-brands fa-github fa-1x  ms-5  mt-2"></i>
        </Col>
                             
      </Row>
      </Container> 
    </div>
  
  )
}

export default Footer