import React from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link } from 'react-router-dom';


function Header() {
  return (
    <div>

      <Navbar bg="white" data-bs-theme="">
        <Container>


          <Navbar.Brand >
            <Link to='/' style={{ textDecoration: "none" }}>
              <img src="https://i.postimg.cc/8Prqb6nf/purple.png" alt="" style={{ height: "70px", borderRadius: "150px" }} />
            </Link>
          </Navbar.Brand>
          <Nav className="me-auto">
            <Navbar.Brand href="#home"><b className='text-secondary fs-2'><span className='text-primary'>V</span>ideo <span className='text-primary'>U</span>ploader</b></Navbar.Brand>

          </Nav>
        </Container>
      </Navbar>

    </div>
  )
}

export default Header

