import React, { useEffect } from 'react'
import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';
import uniqid from 'uniqid';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { addcategory, deleteCat, getAllcat, getVideo, updateCategory } from '../Services/allapis';
import { Trash } from 'react-feather';
// import { Link } from 'react-router-dom';


function Categories() {

  const [cateInputs, setCatInputs] = useState({

    id: "",
    name: "",
    video: []

  })

  const [categories, setCategories] = useState([])

  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const setInputs = (e) => {
    const { value, name } = e.target
    setCatInputs({ ...cateInputs, [name]: value })
  }


  const addData = async () => {
    let id = uniqid()
    setCatInputs({ ...cateInputs, ["id"]: id })

    const { name } = cateInputs
    if (name == "") {

      toast.error('all inputs are required', {
        position: "top-center",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        theme: "dark",

      });
    }
    else {

      //api call
      const result = await addcategory(cateInputs)
      if (result.status >= 200 && result.status < 300) {
        setShow(false);

        getAllCategories()

        toast.success('video added', {
          position: "top-center",
          autoClose: 2000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
          theme: "dark",
        });

      }
    }
  }
  // console.log(cateInputs);

  const getAllCategories = async () => {
    const result = await getAllcat()
    if (result.status >= 200 && result.status < 300) {
      setCategories(result.data);
    }
  }
  // console.log(Categories);

  const removeCategory = async (id) => {
    const result = await deleteCat(id)
    if (result.status >= 200 && result.status < 300) {
      //refresh category list
      getAllCategories()
    }
  }
  useEffect(() => {
    getAllCategories()
  }, [])


  const dragOver = (e) => {
    e.preventDefault()
    console.log("dragged over the category");
  }

  const dropped = async (e, id) => {
    console.log("dropped... cat Id-" + id);

    //video id access
    const videoId = e.dataTransfer.getData("cardId")
    console.log(videoId);

    //access video data from backend
    const { data } = await getVideo(videoId)
    console.log(data);


    //selected dropped category from all categories
    const selectedCategory = categories.find(i => i.id == id)
    console.log(selectedCategory);

    //update category object with video data
    selectedCategory.video.push(data)
    console.log(selectedCategory);


    //api call to update the changed category in backend
    await updateCategory(id, selectedCategory)

    getAllCategories()
  }

  return (
    <div>
      <Button className=' text-secondary mb-5' variant="outline-light" onClick={handleShow} style={{ width: '100%' }}>
        Categories
      </Button>


      {

        categories.length > 0 ? (
          categories.map(i => (

            <div  droppable

              onDragOver={(e) => dragOver(e)}

              onDrop={(e) => dropped(e, i?.id)}

              className='border mt-3 p-3'>

{/* <Link to={'/singleview'}  style={{textDecoration:"none"}}> */}
              <p className='fs-3'>{i.name}</p>
    {/* </Link>  */}
              <div className='text-end'>
                <Trash onClick={() => { removeCategory(i.id) }} size={55} className=''></Trash>
              </div>
              {
                i.video.map(j=>

                  <img className='ms-3' style={{height:'70px', width:'70px'}}  src={j.cover_img} alt="" />

          )
        }
        
         </div>
          
  
          ))
        ) : <h1>No Categories added yet</h1>

      }
 


      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title><p className='text-secondary'><span className='text-white'>Add  New</span> Categories</p></Modal.Title>
        </Modal.Header>
        <Modal.Body className='bg-black text-light'>
          <Form>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>Category Name</Form.Label>

              <Form.Control onChange={(e) => setInputs(e)} name='name'

                type="floatinginput"
                placeholder="Type Here"
                autoFocus
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="dark" onClick={addData}>
            Add
          </Button>
        </Modal.Footer>
      </Modal>
      <ToastContainer />

    </div>

  )
}

export default Categories